import { AuthProvider, DataProvider } from 'react-admin'
import dataProvider from './data/data.provider'
import fakeDataProvider from './data/fake-data.provider'
import noAuthProvider from './auth/no-auth.provider'
import fakeAuthProvider from './auth/fake-auth.provider'

interface Providers {
  readonly authProvider: AuthProvider
  readonly dataProvider: DataProvider
}

// TODO: change to real data
export const providersFactory = (
  env: 'development' | 'test' | 'production',
): Providers => {
  switch (env) {
    case 'production':
      return {
        authProvider: fakeAuthProvider(), // TODO: should be authProvider when demo origin
        dataProvider: dataProvider(),
      }
    case 'development':
      return {
        authProvider: fakeAuthProvider(), // TODO: change to authProvider
        dataProvider: dataProvider(),
      }
    default:
      return {
        authProvider: noAuthProvider(),
        dataProvider: fakeDataProvider(),
      }
  }
}
